import * as React from 'react';
import { useParams, useSearchParams } from 'react-router-dom';

import { Response, truncateStr, uuid } from '@miq/utiljs';

import StaffView from '../Views';

import { THit, TLIB } from './types';
import { useForm } from '@miq/formjs';
import { useRequest } from '@miq/hookjs';
import { LIB } from './models';
import { HitCart, HitDate, HitImg, HitQueries, HitUser } from './components';

const LibHitList = (props: { response: Response }) => {
  const { response: r } = props;

  if (!r.isSuccess) return null;

  return (
    <div>
      {r?.items?.map((item) => {
        return (
          <div className="mb-2 text-sm" key={uuid()}>
            <div className="d-flex">
              <div style={{ width: 48, minWidth: 48 }}>
                <HitImg hit={item} />
              </div>

              <div className="d-grid ms-1">
                <div className="text-ellipsis">
                  <HitUser {...{ hit: item }} className="me-1" />
                  <HitCart {...{ hit: item }} className="me-1 bg-green-100" />
                  {` | `}
                  <HitDate time {...{ hit: item }} className="me-1" />
                </div>
                <div className="text-ellipsis">
                  {item.url}
                  <div className="text-muted" title={item.user_agent}>
                    {truncateStr(item.user_agent, 0, 45)}
                  </div>
                </div>

                <div className="">
                  <HitQueries hit={item} />
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default function LIBUpdateView() {
  const { libSlug } = useParams();
  const form = useForm<TLIB>({});

  const { res: hitRes } = useRequest(() => LIB({ slug: libSlug }).hitList(), {
    refreshDeps: [libSlug],
    onSuccess: ({ data }) => {},
    onError: ({ response }) => {},
  });

  const { res } = useRequest(() => LIB({ slug: libSlug }).retrieve(), {
    refreshDeps: [libSlug],
    onSuccess: ({ data }) => form.setValues(data),
    onError: ({ response }) => {},
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  let instance = LIB(r.data);
  const { name } = instance;

  return (
    <StaffView back="../" title={instance.name} text={`p/${name}/`}>
      <div></div>

      <LibHitList response={new Response<THit>(hitRes)} />
    </StaffView>
  );
}
